import {scaleLinear, svg2element, html2element} from "./helpers.js";


// Should be abstracted out also.
function xaxis_g(y){
	return ` <g class="axis x-axis" transform="translate(0,${y})" fill="none" font-size="10" font-family="sans-serif" text-anchor="middle">`
} // xaxis_g

function yaxis_g(x){
	return `<g class="axis y-axis" transform="translate(${x},0)" fill="none" font-size="10" font-family="sans-serif" text-anchor="end">`
} // yaxis_g

function xaxis_line(x0, x1){
	return `<path class="domain" stroke="currentColor" d="M${x0},6V0.5H${x1}V6"></path>`
} // xaxis_line

function yaxis_line(y0, y1){
	return `<path class="domain" stroke="currentColor" d="M-6,${y0}H0.5V${y1}H-6"></path>`
} // yaxis_line


// This tick just needs to be translatedin the x direction: transform="translate(187,0)"
function xaxis_tick(x, label){
	return `<g class="tick" opacity="1" transform="translate(${x},0)"><line stroke="currentColor" y2="6"></line><text fill="currentColor" y="9" dy="0.71em">${label}</text></g>`;
} // xaxistick

function yaxis_tick(y, label){
	return `<g class="tick" opacity="1" transform="translate(0,${y})"><line stroke="currentColor" x2="-6"></line><text fill="currentColor" y="-9" dy="0.32em">${label}</text></g>`;
} // yaxistick





// For spatial correlations the items can have more than one task at a time. Maybe this should be the standard abstract approach?

export default class StackableSpatialCorrelations{
  constructor(items, svg){
    let obj = this;
  
    obj.items = items;
  
    // , "eff_isen_lost_stator_in" always 0.
	// , "eff_isen_lost_rotor" not in metadata
    obj.ordinals = ["eff_isen", "eff_poly", "alpha_rel_stator_in", "alpha_rel_stator_out", "alpha_rel_rotor_in", "alpha_rel_rotor_out", "alpha_stator_in", "alpha_stator_out", "alpha_rotor_in", "alpha_rotor_out", "eff_isen_lost_stator_out", "eff_isen_lost_rotor_in", "eff_isen_lost_rotor_out", "flow_coefficient", "stage_loading"];
	
	obj.categoricals = []
  
  
    // This should just be created hre, and main can just append it where appropriate.
    obj.svg = svg;
  
    svg.onclick = function(){
		svg.style.display = "none";
	}
	
	
  } // constructor
  
  
  
  
  
  
  show(){
	let obj = this;
	obj.svg.style.display = "";
	obj.update()
  } // show
  
  
  hide(){
	let obj = this;
	obj.svg.style.display = "none";
  } // hide
  
  
  clear(){
    let obj = this;	
	
	// First remove EVERYTHING objects.
	let xg = obj.svg.querySelector("g.x-axis");
	if(xg){xg.remove();}
	
	let yg = obj.svg.querySelector("g.y-axis")
	if(yg){yg.remove()};
	
	let sg = obj.svg.querySelector("g.scores")
	if(sg){sg.remove()};
  } // clear
  
  
  draw(scores){
	let obj = this;
	
	let scale = obj.scales;
	  
	let xaxisg = svg2element( xaxis_g( scale.y.dom2range(0) ) )
	let yaxisg = svg2element( yaxis_g( scale.x.dom2range(0) ) )
	let scoreg = svg2element(`<g class="scores"></g>`)
	obj.svg.appendChild(xaxisg)
	obj.svg.appendChild(yaxisg)
	obj.svg.appendChild(scoreg)
	
	let xaxisline = svg2element( xaxis_line( scale.x.dom2range(-1), scale.x.dom2range(1)) )
	let yaxisline = svg2element( yaxis_line( scale.y.dom2range(-1), scale.y.dom2range(1)) )
	xaxisg.appendChild(xaxisline)
	yaxisg.appendChild(yaxisline)
	
	let xticks = [-1, -0.8, -0.6, -0.4, -0.2, 0.2, 0.4, 0.6, 0.8, 1];
    xticks.forEach(v=>{
		let tick = svg2element( xaxis_tick( scale.x.dom2range(v),v ) );
		xaxisg.appendChild(tick)
	}) // forEach
	
	let yticks = [-1, -0.8, -0.6, -0.4, -0.2, 0, 0.2, 0.4, 0.6, 0.8, 1];
	yticks.forEach(v=>{
		let tick = svg2element( yaxis_tick( scale.y.dom2range(v),v ) );
		yaxisg.appendChild(tick)
	}) // forEach
	
	
	
	
	
	scores.forEach(s=>{
		let x = scale.x.dom2range(s[0])
		let y = scale.y.dom2range(s[1])
		let label = s.name;
		
		
		scoreg.appendChild( svg2element( `<text x="${x}" y="${y}" stroke="black" stroke-width="5" font-family="helvetica" font-size="30">${label}</text>` ) )
    
		let whitetext = svg2element( `<text x="${x}" y="${y}" font-family="helvetica" font-size="30" fill="white" style="cursor: pointer;">${label}</text>` )
		scoreg.appendChild( whitetext )
		
		whitetext.onclick = function(){
			obj.positionByMetadata(s.name, s[0]>s[1] ? "x" : "y")
		} // function
	})
	  
	  
  } // draw
  
  update(){
	let obj = this;
	
	obj.clear();
	
	// When collecting the scores we're banking on the fact that the originally created items are never destroyed. Only the groups get destroyed when no longer needed. Items are always accessed in the same order.
	let scores = obj.ordinalscores.concat( obj.categoricalscores )
	console.log(scores)
	
	obj.draw(scores)
	
	
	return scores;
  } // update
  
  
  
  set items(A){
	let obj = this;
	obj._items = A;
  } // set items
  
  get items(){
	let obj = this;
	// get only the currently active contours. The active ones are those that are either a member of a group that is on-screen, or that are visisble. So then this needs access to the groups?? Or just create a flag to show whether an item was grouped.
	return obj._items.filter(item=>{
		return item.isactive || item.node.style.display != "none";
	}) // filter
	  
  } // get items
  
  
  get scales(){
	let obj = this;
	
	let box = obj.svg.getBoundingClientRect();
	
	let xscale = new scaleLinear();
	xscale.domain = [-1, 1];
	xscale.range = [box.width*0.05, box.width*0.95];
	
	let yscale = new scaleLinear();
	yscale.domain = [1, -1];
	yscale.range = [box.height*0.05, box.height*0.95];
	
	return {x: xscale, y: yscale}
  } // get scales
  
  get positions(){
	let obj = this;
	
	return obj.items.reduce((acc, item)=>{
		acc.x.push(item.node.offsetLeft)
		acc.y.push(item.node.offsetTop)
		return acc
	}, {x: [], y:[]})
  } // get positions
  
  
  get spatialvariables(){
	let obj = this;
	
// Now collect the variables.
	let spatial = obj.positions;
	return [createvariable( spatial.x, "x" ), createvariable( spatial.y, "y" )]
  } // get spatialvariables
  
  
  get ordinalvariables(){
	let obj = this;
	
	// Create an object of arrays, with each representing a variable. Does it for all the items.
	let values = obj.items.reduce((acc, item)=>{
	  obj.ordinals.forEach(varname=>{
		if(acc[varname]){
		  acc[varname].push( item.tasks[0][varname] )
		} else {
		  acc[varname] = [item.tasks[0][varname]]
		} // if
	  })
	  return acc
	},{})
	
	return obj.ordinals.reduce((acc, varname)=>{
		acc.push( createvariable(values[varname], varname) )
		return acc
	}, [])
  } // get ordinalvariables
  
  
  get ordinalscores(){
	let obj = this;
	
	let scores = obj.ordinalvariables.map(ordinal=>{
		let sp = obj.spatialvariables.map(spatial=>{
			return spearman(spatial, ordinal)
		}) // forEach
		
		sp.name = ordinal.name
		return sp
	}) // forEach
	
	return scores
  } // get ordinalscores
  
  get categoricalvariables(){
	// The categoricals need to be mapped into numerical values first. The order for the mapping may be different in hte x and y axes, therefore two separate variables are required for the categoricals.
	  
	// But in the end the scores for all variables are required in two directions. Only the correlations between the spatial and metadata variables are considered (no metadata-metadata correlations.)
	let obj = this;
	let spatial = obj.spatialvariables
	
	return obj.categoricals.map(varname=>{
		
		let values = obj.items.map(item=>{
			return item.tasks[0][varname]
		}) // map
		
		let mapping = categoricalmapping(spatial[0], spatial[1], values)
		
		let mapped = [
		  createvariable( values.map(v=>mapping[v].x), varname ),
		  createvariable( values.map(v=>mapping[v].y), varname )
		]
		mapped.name = varname;
		
		return mapped
	}) // map
  } // get categoricalvariables
  
  get categoricalscores(){
	let obj = this;
	
	let spatial = obj.spatialvariables
	
	let scores = obj.categoricalvariables.map(categorical=>{
		
		sp = [
		  spearman(spatial[0], categorical[0]),
		  spearman(spatial[1], categorical[1])
		]
		
		sp.name = categorical.name
		return sp
	}) // forEach
	
	return scores
  } // get categoricalscores
  
  
  positionByMetadata(variable, axis){
	let obj = this;
	
	// Collect the range of the metadata variable.
	let v = range(obj.items.map(item=>item.tasks[0][variable]));
	
	
	// Create the scale.
	let scale = new scaleLinear();
	scale.domain = v;
	
	if(axis=="x"){
		scale.range = range(obj.positions.x);		
		obj.items.forEach(item=>{
			item.node.style.left = scale.dom2range(item.tasks[0][variable]) + "px"
		})
	} else if (axis=="y"){
		scale.range = range(obj.positions.y);
		obj.items.forEach(item=>{
			item.node.style.top  = scale.dom2range(item.tasks[0][variable]) + "px"
		})
	}// if
  } // positionByMetadata
  
  
  // Testing function
  test(x,y){
	let X = createvariable(x, "x")
	let Y = createvariable(y, "y")
	let result = spearman(x,y)
  } // test
  
} // SpatialCorrelations




function categoricalmapping(x, y, v){
	// This is the mapping of a single metadata variable to numerical values. All that is needed are the x,y, and metadata values.
	let uniquevals = helpers.unique(v) // unique

	// Just assign all unique value their median point no? Or just return hte appropriate dictionary?
	return uniquevals.reduce(function(acc, uniqueval){
		
		
		let xpos = x.filter(function(_x,i){return v[i] == uniqueval})
		let ypos = y.filter(function(_y,i){return v[i] == uniqueval})
		
		
		acc[uniqueval] = {
			x: median(xpos),
			y: median(ypos)
		}
		return acc
		
	}, {}) // reduce
} // categoricalmapping
	







	
function createvariable(A, name){
  // Given an array A add on the mean and standard deviation.
  A.mu = mean(A);
  A.sigma = variance(A)**0.5;
  A.sigma = A.sigma == 0 ? Infinity : A.sigma;
  A.name = name;
  return A
} // variable


function spearman(x,y){
  // The inputs are two variable arrays, which are expected to also have a 'mu' and 'sigma' property.
  // Get Spearman's rank correlation scores for the order in the x direction.
  // (https://en.wikipedia.org/wiki/Spearman%27s_rank_correlation_coefficient)
  // The coefficient is
  // covariance (x_rank, y_rank ) / ( sigma(rank_x) sigma(rank_y) )
  return covariance(x,y) / ( x.sigma*y.sigma )
} // spearman

function covariance(x,y){
	// 'd' is an array of observations. Calculate the covariance between x and the metadata variable.
	let N = x.length
	let s = 0;
	for(var i=0; i< N; i++) {
		s += ( x[i] - x.mu )*( y[i] - y.mu );
	}
	return 1/(N - 1)*s
} // covariance

function variance(x){
	// variance is a special case of covariance.
	return covariance(x,x)
} // variance

function median(numbers) {
	// https://stackoverflow.com/questions/45309447/calculating-median-javascript
    let sorted = numbers.slice().sort((a, b) => a - b);
    let middle = Math.floor(sorted.length / 2);

    if (sorted.length % 2 === 0) {
        return (sorted[middle - 1] + sorted[middle]) / 2;
    }

    return sorted[middle];
} // median

function mean(d){
	return sum(d)/d.length;
} // mean

function sum(objarray, accessor){
	let _accessor = accessor ? accessor : function(d){return d};
	return objarray.reduce((acc,obj)=>{
		return acc += _accessor(obj)
	},0)
} // sum



function range(A){
	// Math.min wants separate inputs.
	return A.reduce((acc, v)=>{
		acc[0] = v < acc[0] ? v : acc[0]
		acc[1] = v > acc[1] ? v : acc[1]
		return acc
	}, [Number.POSITIVE_INFINITY, Number.NEGATIVE_INFINITY])
} // min

