import {html2element, scaleLinear} from "./helpers.js";
import CommentingManager from "../ui/commenting/src/CommentingManager.js";

// Make a plot class that fits in between the specific applications and the wrapper.
import { addDraggingToItem } from "./dragging.js";
import unsteadyLinePlot from "../../plot/unsteadyLinePlot.js";
import unsteadyScatterPlot from "../../plot/unsteadyScatterPlot.js";

// This class should just act as an UI and it should use the constituent items for the actual drawing.
// The main difficulty with this is drawing the standard deviation plot. How would that look like in 3D anyway?


let css = {
  view: `
	width: 300px;
	height: 200px;
  `,
  
  groupbutton: `
	border: none;
	background-color: transparent;
	color: gainsboro; 
	cursor: pointer;
	padding: 2.3px;
	margin-bottom: 10px;
  `,
  
  bookmark: `
    height: 20px; 
	width: 20px; 
	background-color: gainsboro;
  `
}; // css


// The div.item is the top element when the user clicks to drag. The dragging checks whether drag is the appropriate action (as opposed to view interactions) by checking if the event target equals the item node. For the group that is not so because it has an additional wrapper.
let template = `
<div class="item-group" style="position: absolute;">
  <table style="border-collapse: collapse;">
    <tbody>
	<tr>
	  <td>
		<div class="item-proxy" style="box-shadow: 1px 2px 4px 0px rgba(0,0,0,0.25);">
		  <div class="view-proxy"></div>
		  <div class="playcontrols-proxy"></div>
		  <div class="chapterform-proxy"></div>
		  <div class="commenting"></div>
		</div>
	  </td>
	  <td style="vertical-align: top;">
	    <button class="ungroup" style="${ css.groupbutton }">
		  <i class="fa fa-times" style="font-size: 20px;"></i>
		</button>
		 <button class="enter" style="${ css.groupbutton }">
		  <i class="fa fa-sign-in" style="font-size: 20px;"></i>
		</button>
		<button class="dissolve" style="${ css.groupbutton } display:none;">
		  <i class="fa fa-trash-o" style="font-size: 20px;"></i>
		</button>
		<div class="bookmarks">
		</div>
	  </td>
	</tr>
	</tbody>
  </table>
</div>
`; // template


// Maybe this should be just an empty div over the item, and then that item should be shown? And other ones can just be hidden?? Maybe that is simplst - no moving of the controls needed at all. But commenting? How should that work? Just change the commenting?

let bookmarktemplate = `<div class="mark" style="${ css.bookmark }"></div>`;




// The group is still a wrapper node. A whole plot is appended to it with all the necessary items. When the group is disbanded the items should be released.
export default class StackableGroup{
  members = []
  
  constructor(members){
	let obj = this;
	obj.node = html2element(template);
    	
	
	
	// Make hte necessary plot. Make it empty and then append the items directly to avoid reloading all the data.
	// obj.plot = new unsteadyLinePlot([]);
	obj.plot = new unsteadyScatterPlot([]);
	obj.node.querySelector("div.item-proxy").appendChild( obj.plot.node );
	obj.draghandle = obj.plot.draghandle;
	
	obj.members = members;
	members.forEach(member=>{
		obj.add(member)
	}) // forEach
	obj.updateReleaseScales();
	
	
	
	/*
	obj.plot.svgobj.xmenu.variables = [{name: "x", axistype: "linear", extent: [0.288,0.44]}];
	obj.plot.svgobj.ymenu.variables = [{name: "entropy", axistype: "linear", extent: [1140,1260]}];
	obj.plot.svgobj.needsupdate = true;
	*/
	
	obj.plot.svgobj.xmenu.variables = [{name: "blockage_unst", axistype: "linear", extent: [-0.034, -0.023]}];
	obj.plot.svgobj.ymenu.variables = [{name: "eff_lost_unst", axistype: "linear", extent: [0,0.55]}];
	obj.plot.svgobj.needsupdate = true;
	
	
	
	// Calculate where the node should be added. Store the original positions on the items, as long as they are in the group.
	let n = members.length;
	let pos = members.reduce((acc, item)=>{
		acc[0] += parseInt( item.node.style.left )/n;
		acc[1] += parseInt( item.node.style.top )/n;
		return acc
	}, [0,0])
	obj.node.style.left = pos[0] + "px";
	obj.node.style.top = pos[1] + "px";
	
	
	
	
	// Add teh dragging to it.
	addDraggingToItem( obj )
	
	
	
	// Removal of the group.
	obj.node.querySelector("button.ungroup").onclick = function(){
	  obj.remove();
	} // onclick
	
	
	// Entering a group.
	obj.node.querySelector("button.enter").onclick = function(){
		obj.enter()
	} // onclick
	
	
	// Dissolving the annotations.
	obj.node.querySelector("button.dissolve").onclick = function(){
		obj.dissolve()
	} // onclick
	
	
	
  } // constructor
  
  
  
  // proxy
  enter(){
	// Do nothing, it'll be superset outside.
  } // enter
  
  remove(){
	// Destroy the group, reactivate the initial plots.
	let obj = this;
	
	obj.members.forEach(member=>{
		obj.release(member)
	})
	
	// Delete the node.
	obj.node.remove();
  } // remove
  
  release(item){
	// Let's a single data item be released from the group.
	let obj = this;
	
	
	item.items.forEach(ud=>{
		ud.attached = false;
	})
	item.svgobj.needsupdate = true;
	obj.plot.playcontrols.skipped = true;
	
	let pos = obj.calculateReleasePosition(item);
	item.node.style.left = pos[0] + "px";
	item.node.style.top = pos[1] + "px";
	item.node.style.display = "inline-block";
	
	delete item.initialposition
	
  } // release
  
  add(item){
	// Add an item to the group by dragging it over the group.
	let obj = this;
	
	item.initialposition = [parseInt( item.node.style.left ), parseInt( item.node.style.top )];
	
	// No need to do the attaching here - the plot.update does the attaching as long as the 'attached' attribute is set to false. The same technique is used when detaching data.
	item.svgobj.needsupdate = false;
	item.items.forEach(ud=>{
		ud.attached = false;
	}) // forEach
	
	// Now push the promises from this plot to the new one.
	obj.plot.items = obj.plot.items.concat(item.items);
	obj.plot.svgobj.needsupdate = true;
	obj.plot.playcontrols.skipped = true;
	
	item.node.style.display = "none"
	
	
	// THIS CONDITION NEEDS TO BE REWORKED. A plot can be grouped, but the group itself isn't visible. Maybe a signle isactive flag should be given when entering groups etc? Or maybe the groups should update their members based on whether they're visible or not?
	item.isactive = true;
  } // add

  dissolve(){
	let obj = this;
  } // dissolve
  

  
  
  calculateReleasePosition(item){
	let obj = this;
	let x = parseInt(item.node.style.left) + obj.releaseScales.x.dom2range(item.initialposition[0]);
	let y = parseInt(item.node.style.top) + obj.releaseScales.y.dom2range(item.initialposition[1]);
	
	return [x,y]
  } // calculateReleasePosition

  
  updateReleaseScales(){
	let obj = this;
	let domain = obj.members.reduce((acc,member)=>{
		acc.x[0] = acc.x[0] > member.initialposition[0] ? member.initialposition[0] : acc.x[0];
		acc.x[1] = acc.x[1] < member.initialposition[0] ? member.initialposition[0] : acc.x[1];
		
		acc.y[0] = acc.y[0] > member.initialposition[1] ? member.initialposition[1] : acc.y[0];
		acc.y[1] = acc.y[1] < member.initialposition[1] ? member.initialposition[1] : acc.y[1];
		 
		return acc
	}, {x: [Number.POSITIVE_INFINITY, Number.NEGATIVE_INFINITY], y: [Number.POSITIVE_INFINITY, Number.NEGATIVE_INFINITY]})
	
	let xscale = new scaleLinear();
	xscale.domain = domain.x;
	xscale.range = [-50, 50];
	
	let yscale = new scaleLinear();
	yscale.domain = domain.y;
	yscale.range = [-50, 50];  
	
	obj.releaseScales = {x: xscale, y: yscale}
  } // releaseScales
  
  
  
  
  
  hide(){
	let obj = this;
	obj.node.style.display = "none";
	
	obj.plot.items.forEach(item=>item.isactive=false)
  }
  
  
  show(){
	let obj = this;
	obj.node.style.display = "";
	obj.members.forEach(item=>{
		item.node.style.display = "none";
	})
	obj.plot.items.forEach(item=>item.isactive=true)
  }
  
  

} // Group