import { svg2element } from "./playbar/helpers.js"; 



let template_circle = `
<circle r="5" fill="cornflowerblue"></circle>
`; // template_point

let template_path = `
<path r="5" fill="none" stroke="gainsboro" stroke-width="4"></path>
`; // template_point




// The variables should come from here. So when the metadata file for the slice is loaded the variables can be set up. Then when a variable is selected it has to be propagated into here, so that the new data can be loaded. Should I just use mobx to handle that?
// Or maybe just communicate that




export default class unsteadyScatterData{
	
  t = 0;
	
  constructor(d){
	// 'd' is the name of the file that holds the filenames for the available unsteady values.
    let obj = this;
  
    obj.attached = false;
    
	obj.node_current = svg2element( template_circle );
	obj.node_outline = svg2element( template_path );
    
	obj.node_current.onmouseenter = obj.highlight.bind(obj)
	obj.node_current.onmouseleave = obj.unhighlight.bind(obj)

	obj.node_outline.onmouseenter = obj.highlight.bind(obj)
	obj.node_outline.onmouseleave = obj.unhighlight.bind(obj)

    
	// Maybe the data should be reformatted, and the points should be held together? That's how it's done for the unsteady line plots? Anyway, this loading can be resolved later.
	
	
	// But don't I ultimately want to follow this specific approach? Just keep all the variables separate for loading? And only load them when absolutely necessary? However, this object still needs an x and a y variable...
	
	// Note that blockage, eff_lost etc are just calculated for the outlet plane. They could be calculated at any other plane also. In that sense they are also slices.
	
	// For the line plots the x and y are prescribed. Don't allow the user to change this for now. Just keeep it here.
	obj.promises = ["blockage_unst", "eff_lost_unst"].map(variable=>{
		return fetch(d[variable])
		  .then(res=>res.json())
	})
	
	// The outline promise here is a hack....
	obj.timesteps = []
	obj.outlinepromise = Promise.all(obj.promises).then(data=>{
		// data now has eff_lost_unst and blockage_unst. They need to be changed from column data to row data. Maybe it's more convenient to just store it as row data in the first place?
		for(let i=0; i<data[0].length; i++){
			obj.timesteps.push({t: i, x: data[0][i], y: data[1][i]})
		} // for
		
		obj.outline = obj.timesteps;
	}) // then
	
  } // constructor
  
  
  
  
  
  get current(){
	// Just release the timestep that is closest to the specified time.
	let obj = this;
	
	if(obj.timesteps.length > 0){
		// obj.t is the one given by the playbar, the t.t is from the data.
		let t = obj.timesteps.reduce((acc, t)=>{
			return Math.abs(obj.t - acc.t) < Math.abs(obj.t - t.t) ? acc : t;
		}) // reduce

		return t
		
	} else {
		return false
	} // if
  } // get current
  
  
  update(xaxis, yaxis){
	// xaxis and yaxis are passed in because they have the scales to convert the values to screen positions.
	let obj = this;
	
	// This one is just loading all the files belonging to one unsteady case.
	if(obj.timesteps.length > 0){
		if(obj.outline){
			obj.node_outline.setAttribute("d", makeOutlinePath(obj.outline, xaxis.scale, yaxis.scale));
		} // if
		  
		obj.node_current.setAttribute("cx", xaxis.scale(obj.current.x));
		obj.node_current.setAttribute("cy", yaxis.scale(obj.current.y));
	} // if
  } // update
  
  
  highlight(){
	let obj = this;
	// raise = this.parentNode.appendChild(this);
	obj.node_current.setAttribute("fill", "orange")
	obj.node_current.parentNode.appendChild(obj.node_current)
	
	obj.node_outline.setAttribute("stroke", "gray")
	obj.node_outline.parentNode.appendChild(obj.node_outline)
  } // highlight
  
  unhighlight(){
	let obj = this;
	obj.node_current.setAttribute("fill", "cornflowerblue")
	obj.node_outline.setAttribute("stroke", "gainsboro")
  } // unhighlight
  
} // unsteadyScatterData




function makeOutlinePath(points, xscale, yscale){ 
	let p = `M${ xscale( points[0].x ) } ${ yscale( points[0].y ) } `;
	for(let i=1; i<points.length; i++){
		p += `L${ xscale( points[i].x ) } ${ yscale( points[i].y ) } `
	} // if
	return p
} // makeOutinePath



