import {html2element, indexOfObjectByAttr} from "../core/helpers.js";

// Super class.
import dbsliceCrossfilterPlot from "./dbsliceCrossfilterPlot.js";

// Inset components.
import divSelectMenu from "./components/divSelectMenu.js";
import twoInteractiveAxesInset from "./components/twoInteractiveAxesInset.js";
import PlayControls from "./components/playbar/PlayControls.js";

// Can the model inputs be created outside? No- if they are then they will become active in all plots!
import modelInputVariable from "./components/modelInputVariable.js";

// Import the data manager class.
import unsteadyScatterData from "./components/unsteadyScatterData.js";







// The template can now hold one inset per div let's say. Maybe here I want to include a modelInputVariableSelectionInset and a twoInteractiveAxesInset. The drawing on the svg should be implemented here.
let template = `
<div style="width: 400px; padding-bottom: 20px;">
	<div class="scatterplot"></div>
	<div class="playcontrols"></div>
</div>
`




export default class unsteadyLinePlot extends dbsliceCrossfilterPlot{
	width = 400
	
	items = []
	tasks = []
	promises = []
	
	timelastdraw = 0;
	dt = 1000 / 24; // 1s/24fps
	increment = 1;
	
	constructor(tasks, configobj){
		super(configobj);
		let obj = this;
		obj.tasks = tasks;
		
		// Insert the header node as the 'wrappednode' to enable dragging.
		obj.wrappednode = obj.node.querySelector("div.card-header");
		
		// Hard code the name:
		obj.node.querySelector("input.card-title").setAttribute("value", "Outlet" + (tasks[0] ? ": " + tasks[0].taskId : ""))
		
		
		// Append the plot backbone.
		let container = html2element(template)
		obj.node.querySelector("div.card-body").appendChild(container);
		
		// The axes inset.
		obj.svgobj = new twoInteractiveAxesInset([]);
		container.querySelector("div.scatterplot").appendChild(obj.svgobj.node);
		
		// Where should the time variable selection be located? The menu is a div, so it can be attached anywhere. Try just attaching it to the playber?
		obj.playcontrols = new PlayControls();
		container.querySelector("div.playcontrols").appendChild(obj.playcontrols.node);
		obj.playcontrols.x = 40;
		obj.playcontrols.update();
		obj.playcontrols.t_domain = [0, 72];
		
		
		
		
		
		
		
		// obj.counter = [0,0, obj.tasks.length]
		
		
		// I think originally it was the library that will have taken control of loading the files, and just the promiseswould hav ebeen passed to the plots? For simplicity just keep it all here for now.
		obj.promises = obj.tasks.map(m=>{
			let promise = fetch(m.unsteady_scatterplot)
			  .then(res=>res.json())
			  .then(json=>{
				let g = new unsteadyScatterData( json );
				obj.items.push(g)
				
				// Call for update when the outline is computed.
				g.outlinepromise.then(d=>{
					obj.svgobj.needsupdate = true;
				}) // Promise.all
				
				// Drawing can start when the first file is transferred.
				return g.promises[0]
			}) // then
			
			
			return promise
		}) // map
		
		
		// This waits for all the timesteps to be loaded in at once!!
		Promise.any( obj.promises ).then(function(){
			// At what point should the variables get selected? For now just force the x axes to be x, and the y axes to be mach Number
			obj.svgobj.xmenu.variables = [{name: "blockage_unst", axistype: "linear", extent: [0.013, 0.029]}];
			obj.svgobj.ymenu.variables = [{name: "eff_lost_unst", axistype: "linear", extent: [-0.01,0.06]}];
		})
		
		
		
		// How to handle the buffering update? Just keep continuously updating it? Maybe just base the buffering on hte amount of the full data that was loaded in?
		
		
		
		
		obj.svgobj.needsupdate = true;
		obj.update()
		
		
		
		
		// Controls to disband the group. Maybe this should be done outside??
	} // constructor
	
	
	
	
	update(){
		// Update the time of the bar. By how much? How quickly shouldthe movement be made? Update at 24 frames per second? Maybe just move through by indexing? What happens when they have different temporal resolution?
		let obj = this;
		let now = performance.now();
		
		if(now > obj.timelastdraw + obj.dt){
		  if( obj.playcontrols.playing ){
			obj.timelastdraw = now;
			
			
			// Update the playbar
			obj.playcontrols.increment( obj.increment );
			
			// Time is step as opposed to incrementing to allow data with different timesteps to be played alongside one anotehr.
			obj.settimestep( obj.playcontrols.bar.t_play );
			
		  } else if ( obj.playcontrols.skipped ){
			obj.timelastdraw = now;
			
			// Set the data timestep based on the playbar.
			obj.settimestep( obj.playcontrols.bar.t_play );
			
			obj.playcontrols.skipped = false;
		  } // if
		} // if
		
		
		// If it draws at every frame the CPU goes wild. Reduce the drawing to a minimum. However, drawing is required on zoom/pan.
		obj.draw();
		
		
		requestAnimationFrame( obj.update.bind(obj) );
		
	} // update
	
	
	settimestep(t){
		let obj = this;
		// By how much should it be incremented??
		obj.items.forEach(ud=>{
			ud.t = t;
		}) // forEach
		obj.svgobj.needsupdate = true;
	} // settimestep
	
	
	draw(){
	  let obj = this;
	  
	  // Should also be run with no options 
	  let xaxis = obj.svgobj.x;
	  let yaxis = obj.svgobj.y;		
		
	  // At the beginning the plot starts empty.
	  let isPlotConfigured = (
		(xaxis.variable.name != undefined) &&
		(yaxis.variable.name != undefined)
	  );
	  
	  
	  let gdata = obj.svgobj.node.querySelector("g.data");
	  let gback = obj.svgobj.node.querySelector("g.background");
		
	  
	  // The interactions are done on the axes, but we also want the individual data representations to appear when loaded. So they should also have the ability to update themselves.
	  if(isPlotConfigured && obj.svgobj.needsupdate){
		
		
		
		
		// On the first draw the objects are just scheduled for drawing. In subsequent draws they should execute immediately.
		obj.items.forEach(ud=>{
			ud.promises[0].then(d=>{
				if(!ud.attached){
					gback.appendChild(ud.node_outline);
					gdata.appendChild(ud.node_current);
					ud.attached = true;
				} // if				
			
			

				// let t0 = performance.now();
				ud.update( xaxis, yaxis );
				// console.log(`Draw took ${performance.now() - t0}ms`)
			}) // then
		}) // forEach
		
		obj.svgobj.needsupdate = false;
	  } // if
	} // draw
	
	
} // unsteadyLinePlot



function getUpdatedModelInputVariableArray(existing, update){
	// Update modelInputVariable array
	let knownobjs = existing.filter(d=>{
		return update.includes(d.variable);
	}); // filter
	let known = knownobjs.map(mv=>mv.variable); // map
	
	let unknown = update.filter(d=>{
		return !known.includes(d)
	}) // filter
	let unknownobjs = unknown.map(d=>{
		return new modelInputVariable(d)
	}) // map
	
	return knownobjs.concat(unknownobjs)
} // createNeededModelVariables








		