
/* 
0.) HTML ANCHOR LOCATIONS
The anchor locations depend on the index.html file used.
*/
let container = document.getElementById("table-top");
let svg = document.querySelector("svg.lasso");
let correlationssvg = document.querySelector("svg.correlations");



// Import the scatter plot and add one in.
import unsteadyScatterPlot from "./plot/unsteadyScatterPlot.js";

// The small multiple coordinator.
import StackableGroupingCoordinator from "./components/arrangement/StackableGroupingCoordinator.js";
import StackableSpatialCorrelations from "./components/arrangement/StackableSpatialCorrelations.js";

// Try using a single set of metadata. It should have 
fetch("./metadata_smith_sweep_initial.json")
  .then(res=>res.json())
  .then(tasks=>{
	
	let items = [];
	tasks.forEach(task=>{
		let lp = new unsteadyScatterPlot([task]);
		container.appendChild(lp.node);
		items.push(lp)
	})
	// The line plot collects filenames to the actual value files from the unsteady line metadata. The loading is then done in more stages.
	
	
	// The challenge when stacking the svg plots is that they each have their own svg, and therefore the interactions from one plot would have to be relayed to all of them. Furthermore, this get's tricky when the domains aren't the same - for scatter and line plots the comparisons should only be made for correctly aligned data.
	let gc = new StackableGroupingCoordinator(items, container, svg);
	console.log(gc)
	
	
	let sp = new StackableSpatialCorrelations(items, correlationssvg)
	console.log(sp)
	document.querySelector("button.correlations").onclick = function(){
		sp.show()
	} // onclick
  }) // then


